*, *::before, *::after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  font-family: "Space Grotesk", sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  letter-spacing: 0.0625rem;
}

footer {
  margin-top: 1.875rem;
}

ul {
  list-style: none;
}

img, picture, svg, video {
  display: block;
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

button {
  width: 100%;
  background-color: hsl(278deg, 68%, 11%);
  border: none;
  padding: 0.875rem 0;
  border-radius: 0.5rem;
  color: white;
  cursor: pointer;
  transition: 100ms ease-in-out;
}
button:hover {
  background-color: hsl(278deg, 64%, 8%);
}
button:active {
  background-color: hsl(279deg, 55%, 6%);
}

.hidden {
  display: none;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

main {
  margin: auto;
  overflow: hidden;
}
@media (min-height: 46.25em) {
  main {
    height: 100vh;
  }
}
@media (min-width: 68.125em) {
  main {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 0.9375rem;
  }
}

.cardsSection {
  padding: 2.5rem 0;
  position: relative;
  background: url("../images/bg-main-mobile.png") no-repeat;
  background-size: cover;
}
@media (min-width: 25.625em) {
  .cardsSection {
    padding: 2.5rem 0 4.375rem 0;
  }
}
@media (min-width: 68.125em) {
  .cardsSection {
    background: url("/images/bg-main-desktop.png") no-repeat;
    background-size: cover;
    height: 100vh;
    width: 33.3333333333vw;
  }
}

@media (min-width: 68.125em) {
  .cards-container {
    display: table;
    position: absolute;
    width: 100%;
    height: 100%;
  }
  .cards-child-container {
    display: table-cell;
    vertical-align: middle;
  }
  .cards {
    display: flex;
    flex-direction: column-reverse;
  }
}
.card-front, .card-back {
  color: hsl(0deg, 0%, 100%);
  width: 76%;
}
@media (min-width: 36.875em) {
  .card-front, .card-back {
    width: fit-content;
  }
}

.card-back {
  position: relative;
  margin-right: 4%;
  margin-left: auto;
}
@media (min-width: 36.875em) {
  .card-back {
    margin: auto;
    transform: translateX(3.125rem);
  }
}
@media (min-width: 68.125em) {
  .card-back {
    transform: translateX(16.6666666667vw);
  }
}
.card-back__cvc {
  position: absolute;
  letter-spacing: 0.0625rem;
  font-size: 2.5vw;
  top: 19vw;
  left: 80%;
}
@media (min-width: 36.875em) {
  .card-back__cvc {
    font-size: 0.875rem;
    top: 7rem;
    left: 22.5rem;
  }
}
@media (min-width: 68.125em) {
  .card-back__cvc {
    font-size: 1vw;
    top: 8vw;
    left: 80.5%;
  }
}
@media (min-width: 87.375em) {
  .card-back__cvc {
    font-size: 0.875rem;
    top: 7rem;
    left: 22.5rem;
  }
}

.card-front {
  width: fit-content;
  letter-spacing: 1.5px;
}
@media (max-width: 68.0625em) {
  .card-front {
    position: absolute;
  }
}
@media (max-width: 36.8125em) {
  .card-front {
    left: 4%;
    top: calc(2.5rem + 23.65vw);
    padding-right: 20%;
  }
}
@media (min-width: 36.875em) {
  .card-front {
    left: 0;
    right: 0;
    margin: auto;
    padding-right: 10.9375rem;
    top: 11.1875rem;
    transform: translateX(3.125rem);
  }
}
@media (min-width: 68.125em) {
  .card-front {
    transform: translateX(calc(16.6666666667vw - 5.625rem));
    margin-bottom: 2rem;
    padding-right: 0;
  }
}
.card-front__child {
  position: relative;
}
.card-front__logo, .card-front__numbers, .card-front__name, .card-front__expiration {
  position: absolute;
}
.card-front__logo, .card-front__numbers, .card-front__name {
  left: 6.5%;
}
.card-front__logo {
  width: 19%;
  top: 12%;
}
.card-front__numbers {
  bottom: 30%;
  max-width: 80%;
  letter-spacing: 2.5px;
  white-space: nowrap;
}
.card-front__name, .card-front__expiration {
  color: hsl(270deg, 3%, 87%);
  bottom: 15%;
}
.card-front__expiration {
  right: 6.5%;
}
@media (max-width: 25.5625em) {
  .card-front__numbers {
    font-size: 3.4vw;
  }
  .card-front__name, .card-front__expiration {
    font-size: 2vw;
  }
}
@media (min-width: 25.625em) {
  .card-front__numbers {
    font-size: 4.25vw;
  }
  .card-front__name, .card-front__expiration {
    font-size: 2.4vw;
  }
}
@media (min-width: 36.875em) {
  .card-front__numbers {
    font-size: 1.5625rem;
  }
  .card-front__name, .card-front__expiration {
    font-size: 0.875rem;
  }
}
@media (min-width: 68.125em) {
  .card-front__numbers {
    font-size: 1.8vw;
  }
  .card-front__name, .card-front__expiration {
    font-size: 1vw;
  }
}
@media (min-width: 87.375em) {
  .card-front__numbers {
    font-size: 1.5625rem;
  }
  .card-front__name, .card-front__expiration {
    font-size: 0.875rem;
  }
}

.formSection {
  margin-top: 5rem;
  padding: 0 1.625rem;
}
@media (min-width: 36.875em) {
  .formSection {
    margin-top: 6.875rem;
  }
}
@media (min-width: 68.125em) {
  .formSection {
    margin: 0;
    padding: 0 10rem 0 18.75rem;
    display: flex;
    align-items: center;
  }
}
.formSection__form-control {
  display: flex;
  flex-direction: column;
}
.formSection__form-control label {
  font-size: 0.75rem;
  letter-spacing: 0.075rem;
  display: inline-block;
  margin-bottom: 0.375rem;
}
.formSection__form-control input {
  padding: 0.625rem 0.75rem;
  width: 100%;
  border: 1px solid hsl(270deg, 3%, 87%);
  border-radius: 0.5rem;
  margin-bottom: 0.125rem;
  text-transform: uppercase;
}
.formSection__form-control input::placeholder {
  color: hsl(270deg, 3%, 87%);
}
.formSection__form-control input:focus {
  outline: none;
  border: double 1px transparent;
  background-image: linear-gradient(white, white), radial-gradient(circle at top left, hsl(249deg, 99%, 64%), hsl(278deg, 94%, 30%));
  background-origin: border-box;
  background-clip: padding-box, border-box;
}
.formSection__form-control small {
  font-size: 0.625rem;
  color: hsl(0deg, 100%, 66%);
  margin-bottom: 0.875rem;
  visibility: hidden;
}
.formSection__form-control-mmyy {
  display: grid;
  grid-template-columns: 50% 50%;
  width: 98%;
}
.formSection__form-control-my {
  width: 90%;
  display: flex;
  flex-direction: column;
}
.formSection__form-control-cvc {
  width: 100%;
}
.formSection__form-ExpDateCvc {
  display: grid;
  grid-template-columns: 50% 50%;
}

.error input {
  border: 1px solid hsl(0deg, 100%, 66%);
}
.error small {
  visibility: visible;
}

.success input {
  border: 1px solid rgb(12, 167, 94);
}
.success small {
  visibility: hidden;
}

.completeSection {
  width: 100%;
}
.completeSection__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.completeSection img {
  margin-bottom: 2rem;
  margin-top: 1.125rem;
}
.completeSection__thankYou {
  font-size: 1.625rem;
  letter-spacing: 0.25rem;
  color: hsl(278deg, 68%, 11%);
  margin-bottom: 0.625rem;
}
.completeSection__text {
  font-size: 1rem;
  color: hsl(279deg, 6%, 55%);
  margin-bottom: 2.25rem;
}
.completeSection button {
  max-width: 35rem;
}